body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ability img {
  width: 32px;
  height: 32px; }

.hero {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 5px;
  padding-left: 60px;
  padding-right: 10px;
  z-index: 1;
  text-align: left; }
  .hero:hover {
    background-color: rgba(0, 0, 0, 0.2); }
  .hero:before {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: black;
    opacity: .2;
    content: "";
    z-index: -1; }
  .hero .hero__icon {
    width: 32px;
    height: 32px; }
  .hero .hero__name {
    width: 200px; }
  .hero .hero__cost {
    width: 80px;
    color: #E6E6E6;
    text-align: right; }
    .hero .hero__cost span {
      display: inline-block;
      width: 28px;
      height: 28px;
      background: #E6E6E6;
      border-radius: 28px;
      color: #000;
      text-align: center;
      line-height: 28px; }
    .hero .hero__cost__1 {
      color: #B7B7B7; }
    .hero .hero__cost__2 {
      color: #9498BC; }
    .hero .hero__cost__3 {
      color: #4344F4; }
    .hero .hero__cost__4 {
      color: #FD03FB; }
    .hero .hero__cost__5 {
      color: #F98B00; }
  .hero .hero__species {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    text-align: left;
    width: 200px; }
    .hero .hero__species div:nth-of-type(1) {
      margin-right: 20px; }
    .hero .hero__species__Orc {
      color: #E7032B; }
    .hero .hero__species__Beast {
      color: #38820E; }
    .hero .hero__species__Ogre {
      color: #11B7D2; }
    .hero .hero__species__Undead {
      color: #464646; }
    .hero .hero__species__Goblin {
      color: #C53716; }
    .hero .hero__species__Troll {
      color: #8F926B; }
    .hero .hero__species__Human {
      color: white; }
    .hero .hero__species__Demon {
      color: #8715CF; }
    .hero .hero__species__Naga {
      color: #4E48CF; }
    .hero .hero__species__Elf {
      color: #1076CA; }
    .hero .hero__species__Element {
      color: #FBF91C; }
    .hero .hero__species__Satyr {
      color: #8a2aca; }
    .hero .hero__species__Dwarf {
      color: orange; }
    .hero .hero__species__Dragon {
      color: darkred; }
  .hero .hero__ability {
    width: 100px; }
  .hero .hero__className {
    width: 200px; }
    .hero .hero__className__Assassin {
      color: #E7D453; }
    .hero .hero__className__Demon {
      color: #6C0793; }
    .hero .hero__className__Druid {
      color: #D86304; }
    .hero .hero__className__Hunter {
      color: #9ABE66; }
    .hero .hero__className__Knight {
      color: #ED8CB7; }
    .hero .hero__className__Mage {
      color: #66C5E1; }
    .hero .hero__className__Mech {
      color: #B57A00; }
    .hero .hero__className__Shaman {
      color: #2036B0; }
    .hero .hero__className__Warlock {
      color: #5C489F; }
    .hero .hero__className__Warrior {
      color: #B38E61; }

.hero_displayer {
  background-image: url(http://cdn.dota2.com/apps/dota2/images/blogfiles/2014/rightcol_recentposts_bg.jpg);
  background-repeat: repeat;
  width: 700px;
  height: 90vh;
  overflow-y: scroll; }

.component-search-input {
  border-bottom: 1px solid #ccc; }

.component-search-input > div {
  margin: 0 10px 10px 10px; }

.component-search-input input {
  border-radius: 4px;
  border: 1px solid #bbb;
  box-sizing: border-box;
  font-size: 18px;
  padding: 10px 8px;
  width: 100%; }

